const thisYear = new Date();
const Footer = () => (
        <footer className="footer">
        <p>&#x00A9; Daniel McGhee {thisYear.getFullYear()}</p>
        <style jsx>
            {`
                .footer {
                    position: fixed;
                    bottom: 0;
                    left: 0;
                    text-align: center;
                }
            `}
        </style>

        </footer>
);

export default Footer;